import React from 'react'

function About() {
    return (
        <div className="container">
            <div className="py-4">
                <p>It's a single-page application (SPA) which is a web application that 
                interacts with the user by dynamically rewriting the current web page
                with new data from the web server, instead of the default method of the browser
                loading entire new pages. </p> 

                <p>
                It supports full CRUD Functionality. It uses React-router for Dynamic routing.
                </p>

                <p>
                    In this app, you can create a User, view, edit and delete them. For server-side, I used 
                    JSON-Server for fake REST API.
                </p>

                <br />
                <hr />
                <a href="http://ramlakhansingh.pythonanywhere.com/" target="_blank"><h3>My Portfolio</h3></a>
            </div>
        </div>
    )
}

export default About
